import React from "react";
import styled from "styled-components";

import { FlexBoxContentCenter } from "../../0-base/FlexBox";

const StyledHeaderBox = styled(FlexBoxContentCenter).attrs({
  as: "header",
})`
  height: 64px;

  background-color: ${(props) => props.theme.color.ultramarineBlue};

  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px rgba(0, 0, 0, 0.14), 0px 1px 10px rgba(0, 0, 0, 0.12);
`;

export default function HeaderBox(): JSX.Element {
  return <StyledHeaderBox />;
}
