import React from "react";
import styled from "styled-components";

import { FlexBoxContentCenter } from "../../0-base/FlexBox";

const StyledHeaderTitle = styled(FlexBoxContentCenter).attrs({
  as: "h1",
})`
  width: 169px;
  height: 64px;

  background-color: ${(props) => props.theme.color.ultramarineBlue};

  color: ${(props) => props.theme.color.white};
`;

export default function HeaderTitle(): JSX.Element {
  return <StyledHeaderTitle>R.js</StyledHeaderTitle>;
}
